@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,400;1,600&display=swap');

:root {
    --main-font-color: whitesmoke;
    --secundary-font-color: white;
    --secundary-font-color-dark-h1: rgb(228, 222, 222);
    --secundary-font-color-dark: white;
    --secundary-font-familily: 'Nunito Sans', sans-serif;
    --background-color-cards: rgba(27, 166, 201, 0.452);
}


#certificados {

    width: 100%;
    height: auto;
    margin-top: 15vh;

    h2 {
        color: var(--main-font-color);
        font-size: 45px;
        font-weight: 500;
    }

    h3 {
        width: 80%;
        margin: auto;
        color: var(--main-font-color);
        line-height: 30px;
        font-weight: 100;
    }

    .container {
        width: 90%;
        height: 60vh;
        margin: auto;
        margin-top: 15vh;
        display: flex;
        flex-wrap: wrap;

        .kXteup {
            background-color: rgb(36, 35, 35);
        }

        .cVXxbE {
            background-color: #daa520d8;
            box-shadow: rgba(130, 16, 16, 0.1) 0px 20px 25px -5px, rgba(205, 32, 32, 0.04) 0px 10px 10px -5px;
            border: none;
        }

        .kXteup:hover:enabled,
        .kXteup:focus:enabled {
            background-color: rgb(30, 28, 28);
            box-shadow: rgba(58, 57, 57, 0.25) 0px 54px 55px, rgba(159, 158, 158, 0.12) 0px -12px 30px, rgba(74, 73, 73, 0.12) 0px 4px 6px, rgba(170, 166, 166, 0.17) 0px 12px 13px, rgba(135, 135, 135, 0.09) 0px -3px 5px;
        }

        .box {
            width: 85%;
            height: 45vh;
            border: 1px solid rgba(0, 0, 0, 0.171);
            box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
            background-color: var(--background-color-cards);
            background-color: rgb(255, 248, 248);
            margin: auto;
            margin-bottom: 5vh;
            cursor: pointer;

            .imagem {
                width: 100%;
                height: 50%;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .content {
                width: 100%;
                height: 40%;
                margin-top: 25px;
                display: inline-block;

                h4 {
                    width: 80%;
                    text-align: left;
                    color: rgb(77, 77, 77);
                    font-weight: 300;
                    margin-left: 15px;
                    font-size: 20px;
                    margin-top: 0;

                    span {
                        font-weight: bold;
                    }
                }
            }

        }

        .box:hover {
            box-shadow: rgba(0, 0, 0, 0.425) 0px 3px 6px, rgba(8, 116, 218, 0.541) 0px 3px 6px;
        }
    }
}
